@import '../Variables/Variables.scss';

.contact-wrapper {
    height: 100vw;
    display: flex;
}

.contact-box__one {
    background: $white;
    width: 50%;
    
}
.contact-box__two {
    display: flex;
    justify-content: center;
    background: $white;
    width: 50%;

}

.form-wrapper {
    padding: 4rem;
    margin-top: 25%;
}

/* Style inputs with type="text", select elements and textareas */
input[type=text], select, textarea {
    width: 100%;
    padding: 12px;
    border: 2px solid #251762;
    border-radius: 30px;
    //box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    font-size:1rem;
    resize: vertical;
  }
  
  /* Style the submit button with a specific background color etc */
  input[type=submit] {
    background-color: #04AA6D;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  /* When moving the mouse over the submit button, add a darker green color */
  input[type=submit]:hover {
    background-color: #45a049;
  }
  
  /* Add a background color and some padding around the form */
  .container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
  }


.text-area {
    height: 188px;
    border-radius: 30px;
}


